#bin{
    animation-name: pulse;
    animation-timing-function: ease;
    animation-duration: 4s;
    animation-iteration-count: infinite;

}


@keyframes pulse {
    0% {
		transform: scale3d(1.2, 1.2, 1.2);
	}
	50% {
		transform: scale3d(1.5, 1.5, 1.5);
	}
	100% {
		transform: scale3d(1.2, 1.2, 1.2);
	}
}