.diamondOff{
    display: none;
   
}
.diamondActive {
   display: block;
   width: 45px;
   height: 45px;
  position: absolute;
  z-index: 100;
  opacity: 100%;
  animation-name: move; 
  animation-iteration-count: 1; 
  animation-duration: 0.35s; 
  animation-timing-function:ease-in; 
} 
@keyframes move {
  0%{transform: translateY(30px);
    opacity:100%;}
  100%{ opacity:20%; } 
}

#bigDiamondDesktop{
  display: block;
  margin-top: 8%;
  margin-left: auto;
  margin-right: auto;
  z-index: 0;
  width: 30%;
  height: 30%;
}
#bigDiamondMobile{
  display: block;
  margin-top: 10%;
  margin-left: auto;
  margin-right: auto;
  z-index: 0;
  width: 45%;
  height: 45%;
}